.leftMenu{
    width:350px;
    height:100%;
    min-height:100vh;
    background: rgba(34,34,34,0.6);
    backdrop-filter: blur(10px);
    padding: 25px 30px;
    overflow: hidden;
    transition: all 0.3s cubic-bezier(0.175,0.885,0.32,1.275);
    z-index: 1;
}

.logoContainer{
    color:#f1f1f1;
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.searchBox{
    width:100%;
    height:45px;
    background-color: rebeccapurple;
    position:relative;
    margin-top:20px;
    color:#848484;
    overflow:hidden;
    border-radius:10px;
    box-shadow: 0px 0px 20px rgba(0,0,0,0.4);
}
.searchBox input{
    width:100%;
    height:100%;
    outline:none;
    border:none;
    background:rgba(0,0,0,0.5);
    padding:20px;
    font-size: 14px;
    font-weight:bold;

}
.searchIcon{
    position:absolute;
    top: 12px;
    left:4px;
    color:#848484;
    font-size:18px;

}
.MenuContainer{
    margin: 15px 0;
    color: #848484;
}
.MenuContainer.title{
    color:#848484;
    font-size:12 px;
    font-weight: bold;
    margin-bottom:10 px;
    text-transform: uppercase;
}
.MenuContainer ul{
    width:100%;
    padding: 0;
}
.MenuContainer ul li{
    list-style:none;
    margin: 5px 0;
    padding: 5px 0px;
    position:relative;
}
.MenuContainer ul li a{
    text-decoration: none;
    color:#848484;
    display:flex;
    align-items:center;
    font-size:16px;
}
.MenuContainer ul li a i{
    font-size:18px;
    margin-right: 15px;
}
.MenuContainer ul li:hover::before,
.MenuContainer ul li.active::before{
    position:absolute;
    content:"";
    top:0;
    width: 5px;
    height:100%;
    background:#49E12E;
    border-radius: 5px;
    left: -30px;
    transition:0.3s;
}
.MenuContainer ul li:hover a,
.MenuContainer ul li.active a{
    color:#f1f1f1;
    transition:0.3s
}
.playListContainer{
    cursor:pointer;
    margin:15px 0;

}
.playListContainer .nameContainer{
    display:flex;
    justify-content: space-between;
    align-items:center;
    color:#848484;
    margin-bottom:10 px;
}
.playListContainer .nameContainer p{
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
}
.playListContainer .nameContainer i{
    cursor:pointer;
    color:#f1f1f1;
}
.playListContainer .playListScroll{
    height: 100 px;
    overflow-x:hidden ;
    overflow-y: scroll;
    position:relative;
}
.playListContainer .playListScroll .playList{
    display:flex;
    color:#848484;
    justify-content: space-between;
    align-items: center;
    margin: 10px;

}
.playListContainer .playListScroll .playList:hover{
    color:#f1f1f1;
    transition: 0.3s;
}
.playListContainer .playListScroll .playList i{
    font-size: 18px;
}
.playListContainer .playListScroll .playList .list{
    margin-right:15px;
}
.playListContainer .playListScroll .playList .trash{
    margin-left: auto;
}
.playListContainer .playListScroll .playList p{
    font-size:16px;
    text-overflow: ellipsis;
    overflow:hidden;
    display: inline;
}
.trackList{
    border-top:1px solid rgba(225,225,225,0.1);
    padding-top:20 px;
}
.trackList .top{
    display:flex;
    align-items:center;
}
.trackList .top img{
    width: 50px !important;
}
.trackList .top .trackName{
    margin-left:15 px;
    color:#f1f1f1;
    font-size: 14 px;
}
.trackList .top .trackName .trackSpan{
    display:block;
    font-size: 12px;
    color:#848484;
}
.trackList .bottom{
    display:flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    color:#848484
}
.trackList .bottom i{
    font-size:18px;
}
.trackList .bottom i:hover{
    color:#f1f1f1;
    transition:0.3s;
    cursor:pointer;
}
.trackList .bottom input{
    position:relative; 
    height:5px;
    border:none;
    outline:none;
    border-radius:5px;
    background:rgba(225,225,225,0.1);
    appearance: none;
}
.trackList .bottom input:before{
    position: absolute;
    content:'';
    top:0;
    left:0;
    background: #49E12E;
    width:50%;
    height:100%;
    border-radius:10px;
    z-index: 2;
    transition: width 250ms linear;
}
.trackList .bottom input::-webkit-slider-thumb{
    -webkit-appearance: none;
    width:10px;
    height:20px;
    border-radius: 10px;
    background: #f1f1f1;
    border:2px solid #000;
    cursor: pointer;
    position:relative;
    margin:-2px 0 0 0; 
    z-index: 3;
    box-sizing: border-box;
    transition: all 250ms linear;
}
@media screen and (max-width : 500px) {
    .leftMenu{
        width:75px;
        min-width: 75px;
    }
    .leftMenu .searchBox{
        display:none;
    }
    .leftMenu .trackList{
        display:none;
    }
    .leftMenu .playListContainer{
        display: none;
    }
    .leftMenu .MenuContainer ul li  a span{
        display: none;
    }
    .leftMenu .MenuContainer ul li a i{
        font-size:26px;
    }
    .leftMenu .MenuContainer ul li {
        margin:25 px 0;
    }
    .leftMenu .logoContainer h2{
        margin-left: 30px;
    }
}