.mainContainer{
    height:100vh;
    z-index: 1;
    flex-grow:1;
    background: rgba(60, 60, 60, 0.6);
    backdrop-filter: blur(10px);
    transition: all 0.3s cubic-bezier(0.175,0.885,0.32,1.275);

}
.banner {
    height: 300px;
    position: relative;
}
.banner .bannerImg{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    object-fit: cover;
    backdrop-filter: blur(10px);
    filter:grayscale(100);
}
.banner .bottomLayer{
    position:absolute;
    left:0;
    bottom:0;
    width:100%;
    height:5rem;
    background-image: linear-gradient(transparent,rgba(0,0,0,0));
}
.banner .content{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    padding: 20px 30px;
    display: flex; 

}
.banner .content .breadCrump{
    display: flex;
    justify-content: space-between;
    align-items:center;
}
.banner .content .breadCrump p{
    color: #f1f1f1;
    font-size: 14px;
    font-weight: 600;
    letter-spacing:1px;
}
.banner .content .breadCrump i{
    color: #f1f1f1;
}
.banner .content .breadCrump p span{
    color: #f1f1f1;;
}
.banner .content .artist{
    margin-top:auto;
    color:#f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.banner .content .artist .left .name{
    display:flex;
    align-items: center;
}
.banner .content .artist .left .name h2{
    font-size:50px;
}
.banner .content .artist .left .name img{
    height: 35px;
    width: 35px;
}
.banner .content .artist .right a {
    text-align: center;
    text-decoration: none;
    padding: 10 px 30 px;
    font-size:16px;
    color: #f1f1f1;
    margin: 20px 20px;
    border-radius: 100vh;
    font-weight: bold;
}
.banner .content .artist .right a i{
    margin-right: 10px;
    font-size:16 px;
}
.banner .content .artist .right a:nth-child(1){
    background: #39aa24;
}
.banner .content .artist .right a:nth-child(2){
    background: rgba(0,0,0,0.3);
    border: 1px solid rgba(255,255,255,0.1);
}
.banner .content .artist .right a:hover{
    box-shadow: 5px 10px 20px rgba(3, 194, 76, 0.93);
}
.menuList{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    padding: 20px 30 px;
    background: #2c2c2c;
}
.menuList ul{
    display: flex;
    align-items: center;
}
.menuList ul li {
    list-style: none;
    margin: 0 20px;
    position:relative;
}
.menuList ul li a{
    text-decoration: none;
    color:#848484;
    font-size: 14px;
    font-weight: bold;
}
.menuList ul li::before {
    position: absolute;
    content:" ";
    bottom:10 px;
    left:10px;
    width:0 px;
    height:10px;
    border-radius:10px;
    background: #f1f1f1;
}
.menuList ul li:hover::before, 
.menuList ul li.active::before{
    width: 20px;
    transition: width 0.3s cubic-bezier(0.175,0.885,0.32,1.275);
}
.menuList ul li:hover a,
.menuList ul li.active a{
    color:#f1f1f1;
    transition: 0.3s;
}
.audioList{
    padding: 10px 30px;
    width:100%;
}
.audioList .title{
    font-size:18px;
    color:#f1f1f1;
}
.audioList .title span{
    color:#848484;
    font-size:12 px;
}
.songsContainer{
    width:100%;
    height:220px;
    overflow-x: hidden;
    overflow-y: scroll;
}
.songs{
    display:flex;
    align-items: center;
    font-size:14px;
    font-weight: 600;
    color:#848484;
    width:100%;
    margin:10px 0;
    padding:5px;

}
.songs:hover,
.songs.active{
    color:#f1f1f1;
    transition:0.3s;
}
.count{
    margin-right:10px ;
}
.song{
    display:flex;
    align-items: center;
    width:100%;
    border-bottom: 1px solid rgba(255,255,255,0.1);
    cursor:pointer;
}
.song .imgBox{
    width:45px;
    height:45px;
    min-width:45px;
    border-radius:0.5em;
    background: #555;
    margin-right: 10px;
    overflow:hidden;
}
.song .imgBox img{
    width:100%;
    height:100%;
    object-fit:cover;
}
.song .section{
    width:100%;
    display:flex;
    justify-content:space-between;
    align-items: center;
}
.song .section .songName{
    width:50%;
    display:flex;
    justify-content: space-between;
}
.song .section .songName .spanArtist{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.song .section .hits p {
    margin-right: 15px;
}
.song .section .hits .hit i{
    font-size: 12px;
    margin-right:5px;
}
.song .section .hits .favourite i{
    color:#49e12e !important;
    filter:drop-shadow(0px 0px 4px #49e12e);
}

@media screen and (max-width:55px){
    .minContainer{
        min-width:300px;
    }
    .content{
        padding:10px 15px;
    }

    .banner .content .artist .left .name h2{
        font-size:25px;
    }
    .banner .content .artist .right a{
        padding:5px 15px;
        font-size: 12px;
        margin: 0 10px;
    }
    .menuList{
        padding: 5px 10px;
    }
    .menuList ul li{
        margin: 0 5px;
    }
    .menuList p{
        font-size: 12px;
    }
    .menuList p i{
        font-size: 10px;
    }
    .audioList{
        padding:10px 5px;
    }
    .audioList .songs .song .section{
        flex-direction: column;
        align-items: flex-start;
    }
    .audioList .songs .song .section .hits{
        justify-content: space-between;
        margin-top: 10px;
        width:100%;

    }
}